import React from 'react'
import {Paper} from '@material-ui/core'
import {
    Home ,
    AccountBox , 
    Settings , 
    Collections
} from "@material-ui/icons"
import clsx from "clsx"
import { makeStyles } from '@material-ui/core'
import {Link} from "react-router-dom"
import {useSelector} from "react-redux"


const useStyles = makeStyles(theme => ({
    leftSideIcons : {
        display: 'flex' , 
        flexDirection : 'column' , 
        height:  "100vh" ,
         width : "3vw" ,
         justifyContent : 'space-around' ,
         padding: '10px' ,
         background : 'rgba(2 , 4 , 56 ,1)' ,
         color : 'white' , 
         alignItems : 'center' ,
        position: "fixed" , 
        left : "0" ,
        top : "0" ,
        [theme.breakpoints.down("sm")] : {
            width : "100vw" , 
            height: "50px" , 
            flexDirection : "row" ,
            top : "auto" ,
            bottom :"0" ,
            
      }
          
    } ,
    icons : {
        color: 'white' ,
        cursor: 'pointer' ,
        fontSize : "2vw" , 
        [theme.breakpoints.down("sm")] : {
            fontSize :"5vw"
        } ,
        "&:hover ": {
            color : 'yellow'
        } ,
    
    }
}))

const Icons = () => {
    const starterCheck = useSelector(state => state.starterCheck)
    const classes = useStyles()
  return (
   starterCheck && <Paper className={clsx(classes.leftSideIcons)}>
        <Link to="/home" ><Home className={clsx(classes.icons)}></Home></Link>
        <Link to="/skills"><Settings className={clsx(classes.icons)}></Settings></Link>
        <Link to="/projects"><Collections className={clsx(classes.icons)}></Collections></Link>
        <Link to="/about"><AccountBox className={clsx(classes.icons)}></AccountBox></Link>
        
        
        
        
    </Paper>
  )
}

export default Icons