import { htmlProjects } from "../../data/htmlProjectsList";
import { jsProjects } from "../../data/jsProjectsList";
import { reactProjects } from "../../data/reactProjectsList";

const initialState = [{
    name : "" , 
    info : "" , 
    img : "" , 
    link : ""

}]
export const projectReducer = (state = initialState, action )=> {

    switch (action.type) {
        case "HTML":
            return [...htmlProjects]
        case "JS" : 
            return [...jsProjects]
        case "REACT" : 
            return [...reactProjects]    
    
        default:
            return state
            
    }

}