import React , {useState , useEffect, useRef} from 'react'
import { Box , Grid, makeStyles , Typography , Button} from '@material-ui/core'
import clsx from 'clsx'
import { useSelector } from 'react-redux'
import "bootstrap/dist/css/bootstrap.css"


const useStyles = makeStyles(theme=> ({
    root : {
        background : "rgba(0 , 0 , 0 , 0.3)" , 
        height : "80vh" , 
        position: "relative" , 
        bottom: "0" , 
        width : "90vw" , 
        margin : "auto" , 
        borderRadius : "10px" , 
        color : "white" , 
        overflow: "hidden",
        [theme.breakpoints.down("sm")] : {
            width : "90vw" , 
            overflow: "hidden" ,
            
        }
        
    } ,
    container : {
        padding : "0" ,
        display : "flex" , 
        flexWrap : "nowrap" , 
        transition: "transform 0.5s" ,
        [theme.breakpoints.down("sm")] :{
            width : "270vw"
        }
       
    } ,
    item : {
        position: 'relative' , 
        height : "80vh" , 
        display : "flex" , 
        justifyContent : "center" , 
        textAlign : "justify" , 
        
         
     } , 
    list : {
        background: "rgba(12 ,12 , 12 , 1)" ,
        borderRadius : "10px" , 
        color : "whitesmoke" , 
        marginLeft : "0" , 
        "& li" : {
            padding: "5px" ,
            "&:hover" : {
                cursor : "pointer " , 
                color : "#f50c57"
            }
        }
    } , 
    info : {
        justifyContent : "space-between" , 
        flexDirection : "column" ,
        textAlign : "justify" , 
        textJustify : "inter-word" , 
        padding : "10%" ,
        color : "white" ,
        overflowY: "auto",
        "& p" : {
            color : "white" , 
            padding: "0 10% "
        } , 
       
    } , 
    detail : {
        display : "flex" , 
        flexDirection : "column" , 
        alignItems : "center" , 
        justifyContent : "space-between"
    } , 
    projectImg : {
        width : "100%" ,
        height : "auto" ,
        [theme.breakpoints.down("sm" )] : {
            width : "110%" ,
            position : "relative" , 
            right : "-5%"
        }
        
        
    } ,
    btns : {
        display : "flex" , 
        justifyContent : "space-around" , 
        width : "80%" ,
        marginBottom : "15%" ,
        "& button" : {
            width : "40%"
        } 
    } ,
    projectLink : {
         textDecoration : "none" , 
         color : "white" , 
         "&:hover" : {
             color : "white"
         }
     }
    
}))

const ProjectsList = () => {

    const classes = useStyles()
    const projects = useSelector(state=> state.project)
    const [projectIndex , setProjectIndex] = useState(0)
    const itemsRef = useRef()
    const [width , setWidth] = useState(false)
    const containerRef = useRef()
    const [transform ,setTransform] = useState(0)


    const setColor = (value)=> {
        
        Array.from(itemsRef.current.children).forEach((item , index)=> {
            
            if(index === value)
            {
                item.style.color = "#f50c57"
            }else 
            {
                item.style.color = "whitesmoke"
            }
        })
    }

    const handleTransform = (value) => {
        let i = transform

         switch (value) {
            case "positive":
                
                i = i -90 ;
                break;
            case "negative" : 
                
                i = i +90
                break;
            case "reset" : 
                i = 0
                break;
            default:
                
                i = 0
                break;
        }
        
        containerRef.current.style.transform = `translateX(${i}vw)`
        setTransform(i)
    }

    const handleClick = (value)=> {
        setProjectIndex(value)
        setColor(value)
        width && handleTransform("positive")

    }
    const handleWindow = ()=> {

        if(window.innerWidth <= 960)
        {
            setWidth(true)
        }else{
            setWidth(false)
            handleTransform("reset")
        }
        
    }

    useEffect(() => {
      handleWindow()
      window.addEventListener("resize" , handleWindow)
      
      
      return ()=> {
          window.removeEventListener("resize" , handleWindow)
      }
    }, [])
    useEffect(() => {
      setProjectIndex(0)
      handleTransform("reset")
      setColor(0)
    }, [projects])
    
    
    


  return (
    <Box className={clsx(classes.root)} >
        <Grid container spacing={2} className={clsx(classes.container)} ref={containerRef}>
            <Grid item xs={4} className={clsx(classes.item , classes.list)}>
                <ol ref={itemsRef}>
                    {projects.map((item , index) => {
                         return (
                             <li
                             key={index}
                             onClick={()=>handleClick(index)}
                             >{item.name}</li>
                                )
                      })}
                </ol>
            </Grid>
            <Grid item xs={4} className={clsx(classes.item , classes.info)}>
                <Typography className="mt-4">
                {projects[projectIndex] && projects[projectIndex].info}
                </Typography>
                {width && <Button
                     variant='contained'
                     color="secondary"
                     onClick={()=>handleTransform("positive")}
                     className="w-100 mb-5 m-auto"
                     >
                    next
                </Button>}
            </Grid>
            <Grid item xs={4}  className={clsx(classes.item , classes.detail)}>
                <Box component="img"
                     className={clsx(classes.projectImg)}
                     src={`${projects[projectIndex] && projects[projectIndex].img}`}
                ></Box>
                <div className={clsx(classes.btns)}>
                {width && <Button variant="contained"
                        color="primary"
                        onClick={()=> handleTransform("reset")}
                >
                     back
                </Button>}
                <Button variant="contained"
                        color="secondary"
                >
                    <a 
                    href={`${projects[projectIndex] && projects[projectIndex].link}`} 
                    className={clsx(classes.projectLink)}
                    target="_blank"
                    >Go</a>
                </Button>
                </div>
            </Grid>
        </Grid>
    </Box>
  )
}

export default ProjectsList