import { Box , Typography} from '@material-ui/core';
import React , {useEffect, useState}from 'react';
import {makeStyles} from '@material-ui/styles';
import clsx from 'clsx';
import "../styles.css"
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';

const useStyle = makeStyles(theme => ({
      root : {
            margin : "0" ,
            padding : "0" ,
            background: "rgba(0 ,0,0,0.95)",
            display: "flex",
            alignItems : "center" , 
            justifyContent : "center" ,
            color : "white" , 
            fontSize : "3rem" , 
            height : "100vh" , 
            width: "100vw" , 
            
            

      } ,
      text: {
            
            opacity : "1" ,
            animation : "fadeInOut 2.5s ease-in" ,
fontSize : "7vw" ,
            

      }
}))

const Starter = () => {
      const list = ["Hi ," , "I'm hamid Habibi" , "I'm a front-end developer"]
      const [value , setValue] = useState()
      const navigate = useNavigate()
      const classes = useStyle()
      const dispatch = useDispatch()
      useEffect(()=>{
            dispatch({type : "START"})
            let i = 0
            const interval =  setInterval(() => {
                  setValue(list[i])
                  i++
                  if (i >3) {
                        clearInterval(interval)
                        navigate("/home")
                        dispatch({type : "LOADED"})
                  }
            }, 2500);
      } , [])
  return <Box className={clsx(classes.root)}>
          <Typography className={clsx(classes.text)} key={new Date().toString()}>{value}</Typography>
        </Box>;
};

export default Starter;
