import React from 'react';
import {
  Box ,
  makeStyles ,
} from "@material-ui/core"
import clsx from 'clsx';
import { Link , Outlet , useNavigate} from 'react-router-dom'
import "bootstrap/dist/css/bootstrap.css"
import { useRef, useEffect } from 'react';
const useStyles = makeStyles(theme=>({
  root : {
    position: "relative" ,
    height : "100vh"  , 
    width : "100vw" , 
    background: "#595959" ,
    backgroundPosition : "70% 80%" , 
    color : "whitesmoke" ,
    display : "flex" , 
    alignItems : "start" , 
    justifyContent : "space-around" , 
    paddingTop :"5%" ,
    [theme.breakpoints.down("xs")] : {
      flexDirection :"column-reverse" ,
      alignItems : 'center' ,
      height:"100vh" , 
      overflow :"hidden"
    }

  } , 
  profilePic : {
    height : "200px" , 
    width : "200px" , 
    borderRadius :"50%", 
    [theme.breakpoints.down("xs")] : {
     width :"100px" , 
     height :"100px"
    }
  } , 
  infoContainer : {
    width : "60vw" , 
    height : "80vh" , 
    background: "black" ,
    borderRadius : "20px" ,
    boxShadow : "20px 20px 10px #333333" ,
    [theme.breakpoints.down("xs")] : {
      width :"90vw"
    }

  } , 
  navItem : {
    textDecoration :"none" , 
    color : "gray" ,  
    "&:hover" : {
      color :"white"
    }
  } , 
  textWrapper : {
    display : 'flex' , 
    width : "50%" , 
    height : "75%" ,
    margin: "auto" , 
    marginTop : "10%" , 
    overflow: "auto" , 
    textAlign  :"justify" ,
    [theme.breakpoints.down("sm")] : {
      width : "90%" , 
    }
    
  }
}))
const About = () => {
    const classes = useStyles()
    const navRef = useRef()
    const navigate = useNavigate()

    const handleClick = (e)=> {
      const item = e ? e.target: undefined ;
      if(item == undefined) {
        Array.from(navRef.current.children).forEach((btn, index) => {
          if (index == 0) {
            btn.style.color = "white" 
          }
      });
      }else {
        
        Array.from(navRef.current.children).forEach(btn => {
            if (btn == item) {
              btn.style.color = "white"
            }else {
              btn.style.color = "gray"
            }
        });
      }
    }

    useEffect(() => {
      handleClick()
      navigate("./info")
    }, [])
    
  return <Box className={clsx(classes.root)}>
    <Box className={classes.infoContainer}>
      <nav className='d-flex justify-content-around pt-2 ' ref={navRef} >
      <Link 
       to={"./info"} 
       className={clsx(classes.navItem ,)}
       onClick={handleClick}
      >info</Link>
      <Link
       to={"./experience"}
       className={clsx(classes.navItem)}
       onClick={handleClick}
       >contact</Link>
      <a 
        href="https://drive.google.com/file/d/1FmXKMFWff_kdMi2ksw6riPdRg4ViibGA/view?usp=sharing"
        download
        target={"_blank"}  
        className={clsx(classes.navItem)} 
        onClick={handleClick}
        >resume</a>
      </nav>
      
       <div className={clsx(classes.textWrapper)}>
       <Outlet />
       </div>
      
    </Box>
    <Box component="img" 
         src="./img/profilePic.jpeg" 
         className={clsx(classes.profilePic)}     
       />

    
    
  </Box>;
};

export default About;
