import 'bootstrap/dist/css/bootstrap.css';
import React, { useRef } from 'react';
import { useDispatch } from 'react-redux';
import {
  makeStyles , 
  Grid
} from "@material-ui/core"
import clsx from 'clsx';


const useStyles = makeStyles(theme=> ({
  root : {
    height : "100vh" , 
    display:  "flex" , 
    flexDirection : "row" , 
    justifyContent : "center" , 
    alignItems : "center" , 
    transition: "height 0.5s linear",
    [theme.breakpoints.down("xs")] : {
      flexDirection : "column" , 
      
    }

  } , 
  clicked : {
    height : "20vh" , 
    "& div" : {
      backgroundPosition : "left" , 
      backgroundSize : "30% 50%" , 
      transistion : "background-position 0.5s  , background-size 0.5s" , 
      "&:hover" : {
        backgroundPosition : "center" , 
        backgroundSize : "50% 70%" , 
        [theme.breakpoints.down("xs")] : {
      
      
       
          backgroundPosition : "center" ,
          backgroundSize : "80% 70%" , 
          "&:hover" : {
            backgroundSize : "90% 80%"
          }}
       
      } , 
      
      
    } ,
    [theme.breakpoints.down("sm")] : {
      flexDirection : "row" ,
      "& div" : {
        width : "25vw" ,
        height: "15vh" ,
        margin : "5px" ,
         backgroundPosition : "center" ,
         backgroundSize : "80% 70%" , 
         "&:hover" : {
           backgroundSize : "90% 80%"
         }
      } ,
      
    } , 
    
      
    
  } ,
  items : {
    background:  "white" , 
    width: "27%" , 
    height: "80%" , 
    borderRadius : "20px" , 
    cursor: "pointer" , 
    margin: "15px" , 
    [theme.breakpoints.down("sm")] : {
      height : "40%" , 
       
    } ,
    [theme.breakpoints.down("xs")] : {
      height : "20%" , 
      width : "80%" , 
    }
  } , 
  html : {
    backgroundColor : "rgba(235 , 235 , 235 , 0.2)" ,
    background: "url('./img/html-logo.png')" , 
    backgroundSize : "100% 70%" , 
    backgroundRepeat : "no-repeat" , 
    backgroundPosition : "center" , 
    backgroundBlendMode :"dark" , 
    transition: "background-size 0.5s , background-posistion 0.5s ",
    "&:hover" : {
      backgroundSize : "120% 85%"
    } , 
    [theme.breakpoints.down("xs")] : {
      backgroundPosition : 'center' , 
      backgroundSize : "40% 80%" ,
      '&:hover' : {
        backgroundSize : "50% 95%"
      }
    }
  } , 
  js : {
    backgroundColor : "rgba(235 , 235 , 235 , 0.2)" ,
    background: "url('./img/js-logo.png')" , 
    backgroundSize : "70% 65%" , 
    backgroundRepeat : "no-repeat" , 
    backgroundPosition : "center" , 
    backgroundBlendMode :"dark" , 
    transition: "background-size 0.5s",
    "&:hover" : {
      backgroundSize : "85% 75%"
    } , 
    [theme.breakpoints.down("xs")] : {
      backgroundPosition : 'center ' , 
      backgroundSize : "30% 70%" , 
      '&:hover' : {
        backgroundSize : "35% 85%"
      }
    }
  } , 
  react : {
    backgroundColor : "rgba(235 , 235 , 235 , 0.2)" ,
    background: "url('./img/react-logo.png')" , 
    backgroundSize : "120% 65%" , 
    backgroundRepeat : "no-repeat" , 
    backgroundPosition : "center" , 
    backgroundBlendMode :"dark" , 
    transition: "background-size 0.5s",
    "&:hover" : {
      backgroundSize : "150% 85%"
    } , 
    [theme.breakpoints.down("xs")] : {
      backgroundPosition : 'center' , 
      backgroundSize : "50% 80%" , 
      '&:hover' : {
        backgroundSize : "75% 105%"
      }
    }
  } 
  
}))
const ProjectsItems = () => {
  
  const dispatch = useDispatch()
  const containerRef = useRef()
  const classes = useStyles()


  const showList =(value)=> {
    switch (value) {
      case  "html"   :
        dispatch({type : "HTML"})
        break;

      case  "js"     : 
        dispatch({type : "JS"})
        break;

      case "react"   : 
        dispatch({type : "REACT" })
        break;

      default:
        break;
    }
  }
  const handleClick = (e)=> {
    containerRef.current.classList.add(classes.clicked)
    showList(e.target.getAttribute("value"))
  }
  return <Grid container ref={containerRef}  className={clsx(classes.root )}>
    <Grid 
      item
      className={clsx(classes.items , classes.html) }
      value="html"
      onClick={handleClick}
    ></Grid>
    <Grid 
      item 
      className={clsx(classes.items , classes.js)}
      value="js" 
      onClick={handleClick}
    ></Grid>
    <Grid 
      item
      className={clsx(classes.items , classes.react)}
      value="react" 
      onClick={handleClick}
    ></Grid>
  </Grid>;
};

export default ProjectsItems;
